// app/products/[slug]/page.tsx

import LandingPageBuilder from "@/app/landing-pages/new/page";

export default function ProductLandingPageBuilder({
  params,
}: {
  params: { slug: string };
}) {
  const rawSlug = params.slug ?? ""; // Safely handle undefined

  console.log("Raw slug from URL:", rawSlug);

  // Handle empty slug early
  if (!rawSlug) {
    return (
      <div className="flex h-screen items-center justify-center flex-col text-center px-4">
        <p className="text-xl text-red-600">No product specified</p>
        <p className="text-muted-foreground mt-4">
          Please provide a product ID in the URL, e.g., /products/32
        </p>
      </div>
    );
  }

  const productId = Number(rawSlug);

  if (!isNaN(productId) && productId > 0) {
    return <LandingPageBuilder productId={productId} />;
  }

  return (
    <div className="flex h-screen items-center justify-center flex-col text-center px-4">
      <p className="text-xl text-red-600">
        Invalid product slug: "{rawSlug}"
      </p>
      <p className="text-muted-foreground mt-4">
        Currently only numeric product IDs are supported (e.g., /products/32).
      </p>
    </div>
  );
}